
body > .sign-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
body > .sign-wrapper #particles-js {
  width: 100%;
  height: 100%;
}
.sign-wrapper .sign-dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 300px;
  margin-left: -150px;
  margin-top: -150px;
  text-align: center;
}
.sign-wrapper .sign-dialog .ivu-tabs-bar {
  border-bottom: 0;
  margin-bottom: 30px;
}
.sign-wrapper .sign-dialog .ivu-tabs-bar .ivu-tabs-nav-scroll {
  width: 150px;
  margin: 0 auto;
}
.sign-wrapper .sign-dialog .ivu-tabs-bar .ivu-tabs-tab {
  font-size: 16px;
}



