
.article-item {
  margin-bottom: 15px;
  display: block;
  color: #495060;
}
.article-item:hover {
  color: #495060;
}
.article-item .title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.article-item .description {
  word-break: break-all;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.article-item .footer {
  margin-top: 10px;
  border-top: 1px solid #e9eaec;
  padding-top: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.article-item .footer .tags {
  width: calc(100% - 200px);
}
.article-item .footer .author {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  width: 200px;
  text-align: right;
  font-size: 12px;
  color: #80848f;
}
.article-item .footer .author .icon {
  margin: 0 5px;
}



