
.sign-wrapper .get-code .code-item {
  width: calc(100% - 100px);
  position: relative;
}
.sign-wrapper .get-code .ivu-form-item-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.sign-wrapper .get-code .btn-get-code {
  width: 100px;
  border: 1px solid #dddee1;
  border-radius: 0;
  border-top: 0;
  border-left: 0;
}
.sign-wrapper .regist-btn {
  height: 35px;
  margin-top: 20px;
}



