
.setting-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.setting-wrapper .setting-menu {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 200px;
          flex: 0 0 200px;
}
.setting-wrapper .setting-menu a {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /*align-items: center;*/
  padding: 10px 15px;
  color: #495060;
}
.setting-wrapper .setting-menu a i {
  font-size: 18px;
  margin-right: 10px;
}
.setting-wrapper .setting-menu a.router-link-active {
  background-color: #e9eaec;
}
.setting-wrapper .setting-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 20px;
}



