
.sign-wrapper #captcha {
  margin-top: 20px;
}
.sign-wrapper .form-item {
  position: relative;
  margin-bottom: 0;
}
.sign-wrapper .form-item input {
  font-size: 1.4rem;
  height: 40px;
  border-radius: 0;
  border-color: #dddee1 !important;
}
.sign-wrapper .form-item input:focus,
.sign-wrapper .form-item input:hover {
  -webkit-box-shadow: none !important;
          box-shadow: none !important;
  border-color: #dddee1;
}
.sign-wrapper .form-item + .form-item input {
  border-top: 0;
}
.sign-wrapper .form-item .hint {
  position: absolute;
  right: 1px;
  top: 1px;
  bottom: 1px;
  padding: 0 10px;
  color: #ed3f14;
  line-height: 40px;
  font-size: 1.4rem;
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff 8px);
}
.sign-wrapper .form-item .login-btn {
  height: 35px;
}
.sign-wrapper .slide-fade-enter-active,
.sign-wrapper .slide-fade-leave-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.sign-wrapper .slide-fade-enter,
.sign-wrapper .slide-fade-leave-to {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
  opacity: 0;
}
.sign-wrapper .ivu-form-item-error-tip {
  display: none;
}



