
html {
  font-size: 62.5%;
}
html input {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}
main.container {
  padding-top: 20px;
}
body {
  position: absolute !important;
  top: 0;
  left: 0;
  right: 0;
  padding-bottom: 110px;
  min-height: 100%;
  font-size: 14px;
}
ol {
  list-style-type: decimal;
}
ul {
  list-style-type: disc;
}
.container {
  padding: 0 15px;
  width: 992px;
  margin: 0 auto;
}
@media (max-width: 992px) {
.container {
    width: 768px;
}
}
@media (max-width: 768px) {
.container {
    width: 100%;
}
}
.text-center {
  text-align: center;
}



